.container {
  width: 100vw;
  height: 1px;
  min-height: calc(100vh - 62px);

  .wrapper {
    width: 90vw;
    height: 100%;
    margin: 0 auto;
    padding: 25px 5vw 0 5vw;

    .innerWrapper {
      height: 100%;
      display: flex;
      flex-direction: column;
    }
  }
}

